import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Pagination, PaginationItem, PaginationLink, Row, Table } from 'reactstrap';
import { Popover, PopoverBody, PopoverHeader, Alert, Button } from 'reactstrap';
import { Modal, ModalBody, ModalFooter, ModalHeader, Label, Input, InputGroup, InputGroupAddon, InputGroupText, FormGroup } from 'reactstrap'
import { loadLocalAccount, startPlayer, getCurrentHost } from '../js/gegeUtil.js'
import PopHint from '../../conponents/PopHint/PopHint'
import styles from './style.css';
const get = require('simple-get')
const randombytes = require('randombytes')
class NewsWeb extends Component {

  constructor(props) {
    super(props);
    this.page = 1;
    this.state = { list: [], page: 1, count: 0, paying: false, fee: '1', deadline: '5' }
  }

  componentDidMount() {
    var account = loadLocalAccount();
    if (account && account.secret)
      this.secret = account.secret;
    this.setState({ account })
    let hash = window.location.hash;
    let query = null;
    let title = null;
    if (hash) {
      let idx = hash.indexOf("#", 1);
      if (idx > 0) {
        let needPay = hash.substr(idx + 42, 1);
        title = hash.substr(idx + 42);
        title = decodeURIComponent(title)
        if (title) {
          this.setState({ title, needPay })
        }
        query = hash.substr(idx + 1, 40);
        idx = query.indexOf("#", 1);
        if (idx > 0) {

          query = query.substr(0, idx);
        }
        //console.log('query=' + query);
      }
    }
    setTimeout((() => {
      let peerId = null;
      if (!peerId && this.state.account)
        peerId = this.state.account.peerId
      if (!peerId) {
        peerId = this.createTempPeerId();
        this.setState({ peerId })
      } else {
        if (this.isTempPeerId(peerId)) {
          this.setState({ peerId: null })
        }
      }
      let iframeTime = setTimeout(() => {
        //load iframe
        this.loadIframe(peerId, query)
        this.setState({ iframe: true })
      }, 8000);
      if (query)
        startPlayer(peerId, { id: query }, () => {
          //cancel timeout
          clearTimeout(iframeTime)
        })
    }).bind(this), 500);
    //
    //this.doTest()
  }

  loadIframe(peerId, newsId) {
    var page = document.querySelector('#iframe')
    var url = 'http://download.btbresource.com:18080/files/' + peerId + '/' + newsId + '.html';
    page.innerHTML = "<iframe width='100%' height='100%' frameborder='0' name='_blank' src='" + url + "' />"
  }

  isTempPeerId(pid) {
    return pid && pid.startsWith('0000');
  }

  createTempPeerId() {
    let pid = '0000' + randombytes(18).toString('hex')
    return pid;
  }

  togglePurchase() {
    this.setState({ paying: !this.state.paying })
  }

  doPurchase() {

  }

  renderPurchaseDialog() {
    return (<Modal isOpen={this.state.paying} toggle={this.togglePurchase.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.togglePurchase.bind(this)}>支付朋友圈信息</ModalHeader>
      <ModalBody>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>{this.state.title}</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Price(价格):</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" value={1} />
              <InputGroupAddon addonType="append">
                <InputGroupText>BTB</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id="min123" text='Fee(矿工手续费):' content='区块链矿工为账户余额比较多的BTB账户随机产生，负责验证所有交易是否符合规则,并将合法交易保存到区块链。矿工保证整个系统按照规则运行。同时获得交易手续费。' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={(fee) => this.setState({ fee })} placeholder='miner Fee 区块链矿工费，最少1个' />
              <InputGroupAddon addonType="append">
                <InputGroupText>BTB</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id='dl4545' text='Deadline(有效期):' content='矿工验证交易时候会按手续费高低排序处理，有效期内未被处理的交易会被舍弃。交易作废。' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" onChange={(deadline) => this.setState({ deadline })} type="text" placeholder='交易有效期，默认5分钟' />
              <InputGroupAddon addonType="append">
                <InputGroupText>minutes(分钟)</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <div>
          <p>{this.state.purchaseMsg}</p>
        </div>
        <div className="form-actions">
          <Button type="submit" color="primary" onClick={(e) => this.doPurchase()}>Pay(购买)</Button>
          <Button color="secondary" onClick={(e) => this.togglePurchase()}>Cancel(取消)</Button>
        </div>
      </ModalBody>
    </Modal>)
  }

  renderPayButton() {
    if (this.state.needPay == 0)
      return null;
    return (<Alert color="primary">非免费朋友圈信息需要支付后才可以查看全部内容。  <a href='https://github.com/btbresource/btb-android'>点击下载安卓客户端支付</a> </Alert>)
  }

  renderIframe() {
    return (<div id="iframe" style={{ width: '100%' }}>
    </div>)
  }

  renderPlayer() {
    if (this.state.iframe)
      return null;
    return (<div>
      <div id="status">
        <div>
          <code id="numPeers">0 peers(节点)</code>.
                          <code id="downloadSpeed">0 b/s </code> / &#x2197;
                          <code id="uploadSpeed">0 b/s - </code>
          <code id="downloaded"></code> of
                          <code id="total"></code> —
                          <span id="remaining"></span>
        </div>
      </div>
      <div id="player" style={{ width: '100%' }}>
      </div>
    </div>)
  }

  render() {
    return (
      <div id="playdiv">
        <Card>
          <CardHeader>
            {this.renderPayButton()}
            {this.renderPurchaseDialog()}
            {this.state.title}
          </CardHeader>
          <CardBody>
            {this.renderPlayer()}
            {this.renderIframe()}
          </CardBody>
        </Card>

      </div>
    );
  }
}

class PopoverItem extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      popoverOpen: false
    };
  }

  toggle() {
    this.setState({
      popoverOpen: !this.state.popoverOpen,
    });
  }

  render() {
    return (
      <span>
        <span className='alert-primary  fade show' id={'Popover-' + this.props.id} onClick={this.toggle}>
          Share Link(分享链接)
        </span>
        <Popover placement='top' isOpen={this.state.popoverOpen} target={'Popover-' + this.props.id} toggle={this.toggle}>
          <PopoverHeader>Share Link(分享这个资源的链接)</PopoverHeader>
          <PopoverBody>{getCurrentHost() + "/#/btb#" + this.props.res.id}</PopoverBody>
        </Popover>
      </span>
    );
  }
}

export default NewsWeb;



